:root {
  --font-family: var(--font-open-sans), system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  --primary-light: #EEF6FB;
  --primary-medium: #A8D8F0;
  --primary-dark: #0288D1;
  --white: #FFFFFF;
  --light-gray: #F7F9FA;
  --text-primary: #171717;
  --text-secondary: #6B7280;
  --background: var(--white);
  --foreground: var(--text-primary);
  --primary: var(--primary-dark);
  --primary-hover: #0277BD;
  --secondary: var(--light-gray);
  --accent: var(--primary-medium);
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  background-color: var(--background);
  color: var(--foreground);
  font-family: var(--font-family);
  font-size: 16px;
  line-height: 1.6;
  overflow-x: hidden;
  width: 100%;
}
h1 {
  font-size: 32px;
  font-weight: 600;
  line-height: 1.2;
  color: black;
}
h2 {
  font-size: 24px;
  font-weight: 600;
  line-height: 1.3;
  color: black;
}
h3 {
  font-size: 20px;
  font-weight: 600;
  line-height: 1.4;
}
body {
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
}
button {
  font-size: 14px;
  font-weight: 600;
  min-height: 44px;
  min-width: 44px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
.btn-primary {
  background-color: var(--primary);
  color: var(--white);
  border: none;
  padding: 12px 24px;
}
.btn-primary:hover {
  background-color: var(--primary-hover);
  transform: translateY(-1px);
}
.btn-primary:focus {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}
.btn-secondary {
  background-color: transparent;
  color: var(--primary);
  border: 2px solid var(--primary);
  padding: 10px 22px;
}
.btn-secondary:hover {
  background-color: var(--primary);
  color: var(--white);
}
*:focus {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}
html {
  scroll-behavior: smooth;
}
.container-max {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
}
.container-section {
  padding: 60px 0;
  width: 100%;
}
.hero-gradient {
  background: linear-gradient(135deg, var(--primary-light) 0%, var(--white) 100%);
}
.card {
  background: var(--white);
  border-radius: 12px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  padding: 24px;
  transition: all 0.3s ease;
  border: 1px solid #E5E7EB;
  margin-bottom: 24px;
  color: black;
}
.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 25px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  border-color: var(--primary);
}
.form-input {
  width: 100%;
  padding: 14px 18px;
  border: 2px solid #E5E7EB;
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.2s ease;
  background-color: white;
  font-family: inherit;
  min-height: 44px;
  color: var(--text-primary);
}
.form-input:focus {
  border-color: var(--primary);
  outline: none;
  box-shadow: 0 0 0 3px rgba(2, 136, 209, 0.1);
}
@media (max-width: 768px) {
  h1 {
    font-size: 24px;
  }
  h2 {
    font-size: 20px;
    color: black;
  }
  .container-max {
    padding: 0 16px;
  }
  .container-section {
    padding: 40px 0;
  }
}
@media (prefers-color-scheme: dark) {
  :root {
    --foreground: #f3f4f6;
    --background: #111827;
    --primary: #3b82f6;
    --primary-hover: #2563eb;
    --secondary: #1f2937;
  }
}
.text-primary-dark {
  color: var(--primary-dark);
}
.bg-primary-light {
  background-color: var(--primary-light);
}
.text-content {
  color: var(--text-primary);
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.fade-in {
  animation: fadeIn 0.6s ease-out;
}
.spinner {
  border: 2px solid #f3f3f3;
  border-top: 2px solid var(--primary);
  border-radius: 50%;
  width: 20px;
  height: 20px;
  animation: spin 1s linear infinite;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
